.body{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top: 70px;
    margin-left: 175px;
    margin-right: 175px;
    display: flex;
    flex-wrap: wrap;
}
@media (max-width: 1024px) {
    .body{
        margin-left: 60px;
    margin-right: 60px;
    }
}
@media (max-width: 768px) {
    .body{
        margin-left: 10px;
    margin-right: 10px;
    }
}
