.CardContainer{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}


.wrapper {
    padding-top: 120px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.containercard {
    height: 480px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
}

.card {
    width: 100px;
    border-radius: .75rem;
    background-size: cover;
    cursor: pointer;
    overflow: hidden;
    border-radius: 2rem;
    margin: 0 10px;
    display: flex;
    align-items: flex-end;
    transition: .6s cubic-bezier(.28,-0.03,0,.99);
    box-shadow: 0px 10px 30px -5px rgba(0,0,0,0.8);
}

.card > .row {
    color: white;
    display: flex;
    flex-wrap: nowrap;
}

.card > .row > .icon {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 900;
    background: #fff3dd;
    color: orange;
    border-radius: 50%;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
}

.card > .row > .descriptioncard {
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    height: 80px;
    width: 520px;
    opacity: 0;
    transform: translateY(30px);
    transition-delay: .3s;
    transition: all .3s ease;
}

.descriptioncard p {
    color: #b0b0ba;
    padding-top: 5px;
}

.descriptioncard h4 {
    text-transform: uppercase;
}

.containercard > input {
    display: none;
}

input:checked + label {
    width: 770px;
}

input:checked + label .description {
    opacity: 1 !important;
    transform: translateY(0) !important;
}

.card[for="c1"] {
    background-image: url(/src/components/56789.jpg);
}
.card[for="c2"] {
    background-image: url(https://i.imgur.com/xfVrVu2.png);
}
.card[for="c3"] {
    background-image: url(/src/components/13371.png);
}
.card[for="c4"] {
    background-image: url(/src/components/155.png);
}
@media (max-width: 768px) {
    .wrapper{
        display: none;
    }
}
@media (max-width: 1024px){
    .card {
        width: 50px;
    }
}